                :placeholder="t('placeholder.protocol')"
                class="flex-1 rounded border border-slate-300 p-2 text-sm"
              />
              <button
                @click="copyToClipboard(parsedUrl.protocol)"
                class="text-slate-400 hover:text-primary-600 flex-shrink-0 p-1.5"
                :title="t('btn.copy')"
              >
                <img
                  src="https://unpkg.com/lucide-static@latest/icons/copy.svg"
                  :alt="t('btn.copy')"
                  class="w-4 h-4"
                />
              </button>
            </div>
          </div>
          <div class="md:col-span-6">
            <label class="block text-sm font-medium text-slate-700 mb-1"
              >{{ t('URL.Hostname') }}</label
            >
            <div class="flex items-center space-x-2">
              <input
                type="text"
                id="hostname"
                v-model="parsedUrl.hostname"
                :placeholder="t('placeholder.hostname')"
                class="flex-1 rounded border border-slate-300 p-2 text-sm"
              />
              <button
                @click="copyToClipboard(parsedUrl.hostname)"
                class="text-slate-400 hover:text-primary-600 flex-shrink-0 p-1.5"
                :title="t('btn.copy')"
              >
                <img
                  src="https://unpkg.com/lucide-static@latest/icons/copy.svg"
                  :alt="t('btn.copy')"
                  class="w-4 h-4"
                />
              </button>
            </div>
          </div>
          <div class="md:col-span-3">
            <label class="block text-sm font-medium text-slate-700 mb-1"
              >{{ t('URL.Port') }}</label
            >
            <div class="flex items-center space-x-2">
              <input
                type="text"
                id="port"
                v-model="parsedUrl.port"
                :placeholder="t('placeholder.port')"
                class="flex-1 rounded border border-slate-300 p-2 text-sm"
              />
              <button
                @click="copyToClipboard(parsedUrl.port)"
                class="text-slate-400 hover:text-primary-600 flex-shrink-0 p-1.5"
                :title="t('btn.copy')"
              >
                <img
                  src="https://unpkg.com/lucide-static@latest/icons/copy.svg"
                  :alt="t('btn.copy')"
                  class="w-4 h-4"
                />
              </button>
            </div>
          </div>
        </div>

        <!-- 路径部分 -->
        <div>
          <label class="block text-sm font-medium text-slate-700 mb-1"
            >{{ t('URL.Path') }}</label
          >
          <div class="flex items-center space-x-2">
            <input
              type="text"
              id="path"
              v-model="parsedUrl.pathname"
              :placeholder="t('placeholder.path')"
              class="flex-1 rounded border border-slate-300 p-2 text-sm"
            />
            <button
              @click="copyToClipboard(parsedUrl.pathname)"
              class="text-slate-400 hover:text-primary-600 flex-shrink-0 p-1.5"
              :title="t('btn.copy')"
            >
              <img
                src="https://unpkg.com/lucide-static@latest/icons/copy.svg"
                :alt="t('btn.copy')"
                class="w-4 h-4"
              />
            </button>
          </div>
        </div>

        <!-- 查询参数 -->
        <div>
          <div class="flex justify-between items-center mb-2">
            <label class="text-sm font-medium text-slate-700">{{ t('URL.QueryParams') }}</label>
            <div class="space-x-2">
              <button
                @click="addQueryParam()"
                class="text-sm text-primary-600 hover:text-primary-700 flex items-center"
              >
                <img
                  src="https://unpkg.com/lucide-static@latest/icons/plus.svg"
                  :alt="t('Params.add')"
                  class="w-4 h-4 mr-1"
                />
                {{ t('Params.add') }}
              </button>
            </div>
          </div>
          <div class="space-y-2">
            <!-- 确保至少显示一个输入行 -->
            <div v-if="queryParams.length === 0" class="flex flex-wrap items-center gap-2">
              <div class="flex items-center space-x-2 flex-1 min-w-0">
                <input
                  type="text"
                  class="w-1/3 rounded border border-slate-300 p-2 text-sm"
                  :placeholder="t('placeholder.paramName')"
                  @input="e => handleEmptyParamInput(e, 'name')"
                />
                <span class="text-slate-400">=</span>
                <input
                  type="text"
                  class="flex-1 min-w-0 rounded border border-slate-300 p-2 text-sm"
                  :placeholder="t('placeholder.paramValue')"
                  @input="e => handleEmptyParamInput(e, 'value')"
                />
              </div>
              <div class="flex items-center gap-1">
                <button
                  class="p-1.5 text-slate-400 hover:text-primary-600 flex-shrink-0 opacity-50 cursor-not-allowed"
                  :title="t('btn.encode')"
                  disabled
                >
                  <img
                    src="https://unpkg.com/lucide-static@latest/icons/code.svg"
                    :alt="t('btn.encode')"
                    class="w-4 h-4"
                  />
                </button>
                <button
                  class="p-1.5 text-slate-400 hover:text-primary-600 flex-shrink-0 opacity-50 cursor-not-allowed"
                  :title="t('btn.copy')"
                  disabled
                >
                  <img
                    src="https://unpkg.com/lucide-static@latest/icons/copy.svg"
                    :alt="t('btn.copy')"
                    class="w-4 h-4"
                  />
                </button>
                <button
                  class="p-1.5 text-slate-400 hover:text-red-600 flex-shrink-0 opacity-50 cursor-not-allowed"
                  :title="t('btn.delete')"
                  disabled
                >
                  <img
                    src="https://unpkg.com/lucide-static@latest/icons/trash-2.svg"
                    :alt="t('btn.delete')"
                    class="w-4 h-4"
                  />
                </button>
              </div>
            </div>
            
            <!-- 现有参数项 -->
            <div 
              v-for="(param, index) in queryParams" 
              :key="index" 
              class="flex flex-wrap items-center gap-2"
            >
              <div class="flex items-center space-x-2 flex-1 min-w-0">
                <input
                  type="text"
                  v-model="param.name"
                  class="w-1/3 rounded border border-slate-300 p-2 text-sm"
                  :placeholder="t('placeholder.paramName')"
                />
                <span class="text-slate-400">=</span>
                <input
                  type="text"
                  v-model="param.value"
                  class="flex-1 min-w-0 rounded border border-slate-300 p-2 text-sm"
                  :placeholder="t('placeholder.paramValue')"
                />
              </div>
              <div class="flex items-center gap-1">
                <button
                  @click="toggleEncoding(param)"
                  class="p-1.5 text-slate-400 hover:text-primary-600 flex-shrink-0"
                  :title="param.encoded ? t('btn.decode') : t('btn.encode')"
                >
                  <img
                    src="https://unpkg.com/lucide-static@latest/icons/code.svg"
                    :alt="param.encoded ? t('btn.decode') : t('btn.encode')"
                    class="w-4 h-4"
                  />
                </button>
                <button
                  @click="copyToClipboard(`${param.name}=${param.value}`)"
                  class="p-1.5 text-slate-400 hover:text-primary-600 flex-shrink-0"
                  :title="t('btn.copy')"
                >
                  <img
                    src="https://unpkg.com/lucide-static@latest/icons/copy.svg"
                    :alt="t('btn.copy')"
                    class="w-4 h-4"
                  />
                </button>
                <button
                  @click="removeQueryParam(index)"
                  class="p-1.5 text-slate-400 hover:text-red-600 flex-shrink-0"
                  :title="t('btn.delete')"
                >
                  <img
                    src="https://unpkg.com/lucide-static@latest/icons/trash-2.svg"
                    :alt="t('btn.delete')"
                    class="w-4 h-4"
                  />
                </button>
              </div>
            </div>
          </div>
        </div>

        <!-- 片段标识符 -->
        <div>
          <label class="block text-sm font-medium text-slate-700 mb-1"
            >{{ t('URL.Fragment') }}</label
          >
          <div class="flex items-center space-x-2">
            <input
              type="text"
              id="fragment"
              v-model="parsedUrl.hash"
              :placeholder="t('placeholder.fragment')"
              class="flex-1 rounded border border-slate-300 p-2 text-sm"
            />
            <button 
              @click="copyToClipboard(parsedUrl.hash.replace('#', ''))"
              class="text-slate-400 hover:text-primary-600 flex-shrink-0"
              :title="t('btn.copy')"
            >
              <img
                src="https://unpkg.com/lucide-static@latest/icons/copy.svg"
                :alt="t('btn.copy')"
                class="w-4 h-4"
              />
            </button>
          </div>
        </div>

        <!-- 更新按钮 -->
        <div class="pt-4">
          <button
            id="updateBtn"
            @click="updateUrl"
            class="w-full px-6 py-2.5 bg-gradient-to-r from-primary-600 to-primary-700 hover:from-primary-700 hover:to-primary-800 text-white font-medium rounded-lg shadow-md hover:shadow-lg transition-all flex items-center justify-center"
          >
            <img
              src="https://unpkg.com/lucide-static@latest/icons/refresh-cw.svg"
              :alt="t('Params.update')"
              class="w-5 h-5 mr-2"
            />
            {{ t('Params.update') }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, reactive, watch } from 'vue';
import { copyToClipboard } from "~/utils/clipboard";
import { notifyError } from "~/utils/notification";
import { isUrlEncoded } from "~/utils/url";
import { useI18n } from "vue-i18n";

const { t } = useI18n();

// 响应式数据
const urlInput = ref('');
const parsedUrl = reactive({
  protocol: '',
  hostname: '',
  port: '',
  pathname: '',
  hash: ''
});
const queryParams = ref([]);

// 监听URL输入变化，自动解析
watch(urlInput, (newUrl) => {
  if (newUrl && newUrl.trim()) {
    parseUrl(newUrl);
  }
}, { debounce: 500 }); // 添加防抖，避免用户快速输入时多次解析

// 解析URL函数
const parseUrl = (url) => {
  try {
    const urlObj = new URL(url);
    
    // 基本URL部分
    parsedUrl.protocol = urlObj.protocol.replace(':', '');
    parsedUrl.hostname = urlObj.hostname;
    parsedUrl.port = urlObj.port || (urlObj.protocol === 'https:' ? '443' : '80');
    parsedUrl.pathname = urlObj.pathname;
    parsedUrl.hash = urlObj.hash;
    
    // 解析查询参数
    queryParams.value = [];
    urlObj.searchParams.forEach((value, name) => {
      queryParams.value.push({
        name,
        value,
        encoded: isUrlEncoded(value)
      });
    });
    
    return true;
  } catch (error) {
    // 自动解析时不显示错误通知
    console.error('URL 解析错误:', error);
    return false;
  }
};

// 粘贴URL
const pasteUrl = async () => {
  try {
    const text = await navigator.clipboard.readText();
    urlInput.value = text;
    // URL自动变化会触发解析，不需要手动调用parseUrl
  } catch (error) {
    notifyError(t('messages.clipboardError'));
    console.error('剪贴板访问错误:', error);
  }
};

// 清空所有内容
const clearAll = () => {
  urlInput.value = '';
  parsedUrl.protocol = '';
  parsedUrl.hostname = '';
  parsedUrl.port = '';
  parsedUrl.pathname = '';
  parsedUrl.hash = '';
  queryParams.value = [];
};

// 添加查询参数
const addQueryParam = () => {
  queryParams.value.push({
    name: '',
    value: '',
    encoded: false
  });
};

// 删除查询参数
const removeQueryParam = (index) => {
  queryParams.value.splice(index, 1);
};

// 切换编码状态
const toggleEncoding = (param) => {
  if (param.encoded) {
    // 解码
    try {
      param.value = decodeURIComponent(param.value);
      param.encoded = false;
    } catch (error) {
      notifyError(t('messages.decodeError'));
    }
  } else {
    // 编码
    param.value = encodeURIComponent(param.value);
    param.encoded = true;
  }
};

// 处理空参数输入行的输入
const handleEmptyParamInput = (event, fieldType) => {
  // 一旦用户开始输入，就添加一个真实的参数对象
  const value = event.target.value;
  if (value) {
    const newParam = {
      name: fieldType === 'name' ? value : '',
      value: fieldType === 'value' ? value : '',
      encoded: false
    };
    queryParams.value.push(newParam);
  }
};

// 根据当前表单内容重建URL
const updateUrl = () => {
  try {
    // 创建基本 URL
    const url = new URL(`${parsedUrl.protocol}://${parsedUrl.hostname}`);
    
    // 设置端口
    if (parsedUrl.port) {
      url.port = parsedUrl.port;
    }
    
    // 设置路径
    url.pathname = parsedUrl.pathname;
    
    // 清空现有查询参数
    url.search = '';
    
    // 添加查询参数
    queryParams.value.forEach(param => {
      if (param.name) {
        url.searchParams.append(param.name, param.value);
      }
    });
    
    // 设置片段标识符 (hash)
    url.hash = parsedUrl.hash.startsWith('#') 
      ? parsedUrl.hash 
      : parsedUrl.hash ? `#${parsedUrl.hash}` : '';
    
    // 更新输入框的 URL
    urlInput.value = url.toString();
  } catch (error) {
    notifyError(t('messages.updateError'));
    console.error('URL 更新错误:', error);
  }
};
</script>

